@import url("https://fonts.googleapis.com/css?family=Raleway&display=swap");
.content {
  overflow-y: visible;
  margin-left: auto;
  margin-right: auto; }
  .content .paragraphLink {
    color: #009681;
    font-weight: bold; }

body {
  padding: 0;
  font-family: "Raleway", sans-serif;
  overflow-x: hidden;
  color: #1d2e46; }

h1 {
  text-align: center;
  margin-bottom: 1em; }

.page {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  min-height: 100%;
  height: 100%; }

.document {
  margin-top: 50px;
  margin-bottom: 50px;
  width: 562px;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid #1d2e46;
  padding: 50px 90px;
  background-color: white; }
  @media screen and (max-width: 742px) {
    .document {
      margin-top: 30px;
      padding: 0px 30px;
      width: calc(100vw - 60px);
      border: none; } }

@media screen and (max-width: 742px) {
  .negative-margin {
    margin-top: -50px; } }

.background {
  z-index: -1;
  margin-left: -50%;
  width: 200%;
  height: 100%;
  position: absolute; }
  @media screen and (max-width: 742px) {
    .background {
      display: none; } }

.flex-row {
  display: flex;
  flex-direction: row; }

.flex-col {
  display: flex;
  flex-direction: column; }

@media screen and (max-width: 742px) {
  .hide-small, #KDSheader * {
    display: none !important; } }

@media screen and (min-width: 1350px) {
  .hide-large {
    display: none !important; } }

.show-small {
  display: none !important; }
  @media screen and (max-width: 742px) {
    .show-small {
      display: inherit !important; } }

.show-large {
  display: none !important; }
  @media screen and (min-width: 1350px) {
    .show-large {
      display: inherit !important; } }

.hide-schedule {
  display: none !important; }
  @media screen and (max-width: 1086px) {
    .hide-schedule {
      display: inherit !important; } }

.show-schedule {
  display: none !important; }
  @media screen and (min-width: 1086px) {
    .show-schedule {
      display: inherit !important; } }
