.switch {
  width: 54px;
  height: 26px;
  border-radius: 12px;
  background-color: #CFD0D3;
  position: relative;
  transition: all 0.3s cubic-bezier(0.77, 0.2, 0.05, 0.5); }
  .switch .ball {
    position: absolute;
    left: -1px;
    top: -2px;
    width: 27px;
    height: 27px;
    border-radius: 50%;
    background-color: white;
    border: 2px solid #1d2e46;
    transition: all 0.3s cubic-bezier(0.77, 0.2, 0.05, 0.5); }

.switch.on {
  background-color: #a0d1ca; }
  .switch.on .ball {
    left: calc(100% - 27px + 2px);
    background-color: #1d2e46; }
