.layout {
  min-height: 100%;
  height: 100%;
  height: auto;
  display: flex;
  flex-direction: column; }

.layout .content {
  flex-grow: 1; }

.menuAndHeader .header {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Raleway"; }
  @media screen and (max-width: 742px) {
    .menuAndHeader .header {
      flex-direction: column;
      width: 100%; } }
  .menuAndHeader .header #KDSlogo {
    width: 10em;
    margin-right: 5%; }
    @media screen and (max-width: 742px) {
      .menuAndHeader .header #KDSlogo {
        width: 44px;
        margin-right: 1em; } }
  .menuAndHeader .header .headline {
    float: right;
    margin-bottom: 0.5em;
    font-weight: normal; }
    @media screen and (max-width: 742px) {
      .menuAndHeader .header .headline {
        float: right;
        font-size: 1.1em; } }
  .menuAndHeader .header .header-location {
    margin: 0em;
    font-size: 1em;
    font-weight: normal;
    color: #75787b; }
    @media screen and (max-width: 742px) {
      .menuAndHeader .header .header-location {
        font-size: 0.8em; } }
    .menuAndHeader .header .header-location #location-img {
      width: 1em;
      margin-right: 0.5em; }
  .menuAndHeader .header .header-subheadline {
    margin-top: 0em;
    font-size: 0.8em;
    font-weight: normal; }
    .menuAndHeader .header .header-subheadline .subheadline-link {
      text-decoration: none;
      color: #009681; }
    @media screen and (max-width: 742px) {
      .menuAndHeader .header .header-subheadline {
        font-size: 0.7em; } }

.menuAndHeader #KDSheader {
  display: flex;
  flex-direction: column;
  align-items: center; }

.menuAndHeader {
  min-height: 44px; }
  @media screen and (max-width: 742px) {
    .menuAndHeader {
      margin-top: 30px;
      display: flex;
      align-items: center; } }

.filter-pos {
  position: absolute;
  left: 0px;
  top: 0px; }
