.pin-container {
  top: 0;
  left: 0; }

.pin {
  height: 30px;
  width: 22px;
  margin-left: auto;
  margin-right: auto; }
  .pin .top {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    border-radius: 50%;
    border: 8px solid #1d2e46;
    width: 24px;
    height: 24px; }
  .pin .bottom {
    width: 0;
    height: 0;
    margin-top: -7px;
    margin-left: 1px;
    border-left: 11px solid transparent;
    border-right: 11px solid transparent;
    border-top: 15px solid #1d2e46; }

#favourite-talk-button {
  background-color: Transparent;
  border: none; }

.talk .header {
  height: 100%; }
  .talk .header .diff,
  .talk .header .room,
  .talk .header .time,
  .talk .header .heart {
    margin-top: auto;
    margin-bottom: auto; }
  .talk .header .diff .diff-name,
  .talk .header .room .room-name,
  .talk .header .time .time-text {
    display: block;
    font-size: 0.75rem;
    height: 100%; }
